import { useState } from "react";
import styled from "styled-components";
import numeric2 from "../../../components/img/icon/numeric2.svg";
import cash from "../../../components/img/icon/cash.svg";
import card from "../../../components/img/icon/card.svg";

const Container = styled.div`
  margin-top: 30px;
  border: 1px solid #9ca4aa;
  padding: 16px 22px;
  font-size: 18px;
  @media screen and (max-width: 698px) {
    font-size: 16px;
  }
`;
const Header = styled.div`
  display: flex;
  align-items: center;
`;
const Title = styled.div`
  margin-left: 12px;
`;
const Body = styled.div``;
const Form = styled.form`
  margin-top: 20px;
`;
const FormItemWrapper = styled.div`
  margin-top: 20px;
  padding-bottom: 22px;
  border-bottom: 2px solid #d7dadc;

  ${(props) =>
    props.$isInlineFormItem &&
    `
    padding: 20px;
    background: #F0F1F3;
    border-bottom:0;
  `}
`;

const SubTitle = styled.div`
  margin-top: 16px;
  color: #818b92;
`;
const FormContent = styled.div`
  margin-top: 12px;
`;
const FormRadioLabel = styled.label`
  display: inline-block;
  border: 1px solid #9ca4aa;
  padding: 6px 12px;
  color: #000;
  & + & {
    margin-left: 20px;
  }
  @media screen and (max-width: 698px) {
    & + & {
      margin-top: 20px;
      margin-left: 0px;
    }
    min-width: 100%;
  }
`;
const Span = styled.span`
  margin-left: 12px;
  & + & {
    margin-left: 20px;
  }
  & img {
    width: 25px;
    height: 25px;
    vertical-align: bottom;
  }
`;
const Input = styled.input`
  width: 100%;
  border: 1px solid #d7dadc;
  margin: 0;
  padding: 6px 12px;
`;
const FormNote = styled.div`
  background: #c4c4c4;
  font-size: 16px;
  padding: 6px 12px;
  color: #115460;
`;
const AdviceText = styled.p`
  color: #115460;
  font-size: 16px;
`;
const FormInputLabel = styled.label`
  margin: 10px 0;
  display: block;
  color: #818b92;
`;
const ForCheckboxItem = styled.div`
  @media screen and (max-width: 698px) {
    margin-top: 12px;
  }
`;
const InlineInput = styled.input`
  border: none;
  margin-left: 16px;
  border-bottom: 1px solid #ccc;
  font-size: 16px;
  font-style: italic;
  padding: 0 6px;
`;

const FormBtn = styled.button`
  margin-top: 20px;
  border: none;
  background: #f9897a;
  width: 100%;
  font-size: 18px;
  color: #fff;
  padding: 12px 0;
`;

const payWarnningData = [
  {
    id: 1,
    content:
      "",
  },
  {
    id: 2,
    content:
      "",
  },
  {
    id: 3,
    content: "",
  },
  {
    id: 4,
    content:
      "",
  },
  {
    id: 5,
    content: "",
  },
  {
    id: 6,
    content: "",
  },
  {
    id: 7,
    content: "",
  },
];

const PayWarnningContent = () => {
  return payWarnningData.map((item) => {
    return <p key={item.id}> - {item.content}</p>;
  });
};

const Shipping = () => {
  const [isSameConsignee, setIsSameConsignee] = useState(true);
  const RenderShippingForm = () => {
    return (
      <Form>
        <FormItemWrapper>
          <SubTitle></SubTitle>
          <FormContent>
            <FormRadioLabel>
              <input
                type="radio"
                name="shippingMethod"
                value="blackCat"
                checked={true}
                onChange={null}
                readOnly
              />
              <Span></Span>
              <Span>NT$225</Span>
            </FormRadioLabel>
          </FormContent>
        </FormItemWrapper>
        <FormItemWrapper>
          <SubTitle></SubTitle>
          <FormContent>
            <FormRadioLabel>
              <input
                type="radio"
                name="payMethod"
                value="card"
                checked={true}
                onChange={(e) => e.target.value}
              />
              <Span>
                <img src={card} alt="credit card" />
              </Span>
              <Span></Span>
            </FormRadioLabel>
            <FormRadioLabel>
              <input
                type="radio"
                name="payMethod"
                value="ATM"
                onChange={(e) => e.target.value}
              />
              <Span>
                <img src={cash} alt="cash" />
              </Span>
              <Span>ATM</Span>
            </FormRadioLabel>
          </FormContent>
          <SubTitle></SubTitle>
          <FormContent>
            <Input type="text" name="shipping_note" />
          </FormContent>
          <SubTitle></SubTitle>
          <FormContent>
            <FormNote>
              <PayWarnningContent />
            </FormNote>
          </FormContent>
        </FormItemWrapper>
        <FormItemWrapper>
          <SubTitle></SubTitle>
          <FormContent>
            <FormInputLabel htmlFor="fullname"></FormInputLabel>
            <Input
              type="text"
              id="fullname"
              placeholder="userlastname + firstname"
            />
          </FormContent>
          <FormContent>
            <FormInputLabel htmlFor="phone"></FormInputLabel>
            <Input type="text" id="phone" placeholder="userphone" />
            <AdviceText>
              *100987654321
            </AdviceText>
          </FormContent>
          <FormContent>
            <FormInputLabel htmlFor="address"></FormInputLabel>
            <Input type="text" id="address" placeholder="useraddress" />
          </FormContent>
          <FormContent>
            <FormInputLabel htmlFor="shippingDate"></FormInputLabel>
            <Input type="date" id="shippingDate" />
          </FormContent>
          <FormContent>
            <FormInputLabel htmlFor="last5Number">
              / 
            </FormInputLabel>
            <Input
              type="number"
              id="last5Number"
              placeholder=""
            />
          </FormContent>
        </FormItemWrapper>
        <FormItemWrapper>
          <SubTitle>?</SubTitle>
          <FormContent>
            <FormRadioLabel>
              <input
                type="radio"
                name="invoice"
                value="withPackage"
                checked={true}
                onChange={(e) => e.target.value}
              />
              <Span></Span>
            </FormRadioLabel>
            <FormRadioLabel>
              <input type="radio" name="invoice" value="donate" />
              <Span></Span>
            </FormRadioLabel>
          </FormContent>
          <SubTitle></SubTitle>
          <FormContent>
            <FormRadioLabel>
              <input
                type="radio"
                name="inVoiceType"
                value="normal"
                checked={true}
                onChange={(e) => e.target.value}
              />
              <Span></Span>
            </FormRadioLabel>
            <FormRadioLabel>
              <input type="radio" name="inVoiceType" value="withCompanyNum" />
              <Span></Span>
              <InlineInput
                type="text"
                name="companuNum"
                placeholder=""
              />
            </FormRadioLabel>
            <SubTitle></SubTitle>
            <FormContent>
              <FormNote>
                <h3></h3>
                <p>
                  ()()
                </p>
              </FormNote>
            </FormContent>
          </FormContent>
        </FormItemWrapper>
        <FormItemWrapper>
          <SubTitle></SubTitle>
          <FormContent>
            <FormRadioLabel>
              <input
                type="radio"
                name="consignee"
                value={isSameConsignee}
                checked={isSameConsignee}
                onChange={(e) => setIsSameConsignee(!isSameConsignee)}
              />
              <Span></Span>
            </FormRadioLabel>
            <FormRadioLabel>
              <input
                type="radio"
                name="consignee"
                value={!isSameConsignee}
                checked={!isSameConsignee}
                onChange={(e) => setIsSameConsignee(!isSameConsignee)}
              />
              <Span></Span>
            </FormRadioLabel>
            {!isSameConsignee && (
              <FormItemWrapper $isInlineFormItem={true}>
                <FormContent>
                  <FormInputLabel htmlFor="consignee">
                    
                  </FormInputLabel>
                  <Input type="text" id="consignee" />
                </FormContent>
                <FormContent>
                  <FormInputLabel htmlFor="consigneePhone"></FormInputLabel>
                  <Input type="text" id="consigneePhone" />
                  <AdviceText>
                    *100987654321
                  </AdviceText>
                </FormContent>
                <FormContent>
                  <FormInputLabel htmlFor="consigneeAddress">
                    
                  </FormInputLabel>
                  <Input type="text" id="consigneeAddress" />
                </FormContent>
              </FormItemWrapper>
            )}
            <SubTitle></SubTitle>
            <FormContent>
              <FormNote>
                <div>
                  : <span></span>
                </div>
                <div>
                  : <span>0912345678</span>
                </div>
                <div>
                  : <span>001</span>
                </div>
              </FormNote>
            </FormContent>
          </FormContent>
        </FormItemWrapper>
        <FormItemWrapper>
          <FormContent>
            <ForCheckboxItem>
              <input type="checkbox" name="dataPolicy" />
              <Span></Span>
            </ForCheckboxItem>
            <ForCheckboxItem>
              <input type="checkbox" name="orderPolicy" />
              <Span>
                
              </Span>
            </ForCheckboxItem>
          </FormContent>
        </FormItemWrapper>
        <FormBtn type="submit"></FormBtn>
      </Form>
    );
  };

  return (
    <Container>
      <Header>
        <div>
          <img src={numeric2} alt="shopping shipping cotent" />
        </div>
        <Title></Title>
      </Header>
      <Body>
        <RenderShippingForm />
      </Body>
    </Container>
  );
};

export default Shipping;
